function check(parameter, a, b) {
    it(parameter, () => {
        const s = createConfig()
        checkBounds(s[parameter], a, b)
    })
}
function checkBounds(n, a, b) {
    expect(n).to.be.above(a - 1)
    expect(n).to.be.below(b + 1)
}   